@import url("https://fonts.googleapis.com/css2?family=Mali:wght@500&display=swap");
body, #root, .App {
	width: 100vw;
	height: 100vh;
	margin: 0;
	padding: 0;
	border: none;
}

#root {
	overflow: hidden;
}

* {
	font-family: "Mali", cursive;
}

label {
	cursor: pointer;
}

input[type="text"] {
	border-radius: 50px;
	text-align: center;
}