@import url(https://fonts.googleapis.com/css2?family=Mali:wght@500&display=swap);

body, #root, .App {
	width: 100vw;
	height: 100vh;
	margin: 0;
	padding: 0;
	border: none;
}

#root {
	overflow: hidden;
}

* {
	font-family: "Mali", cursive;
}

label {
	cursor: pointer;
}

input[type="text"] {
	border-radius: 50px;
	text-align: center;
}
#left-menu {
	background: linear-gradient( 90deg, rgb(29, 29, 29), rgb(41, 41, 41), rgb(29, 29, 29));
	width: 20%;
	height: 100%;
	min-width: 250px;
	display: flex;
	flex-direction: column;
	font-size: 1em;
	color: white;
	transition-duration: 0.3s;
}

#left-menu>* {
	margin: 5px 10px;
}

.game-id-container {
	font-size: 1.4em;
	text-align: center;
}

.game-id-label {
	margin-right: 5px;
}

#self-name-input {
	border-radius: 15px;
	padding: 0 10px;
	width: 40%;
}

.form-title {
	font-size: 1.4em;
}

#new-char-name {
	border-radius: 100px;
	padding: 0 15px;
	margin-left: 10px;
	width: 40%;
	font-size: 1em;
}

.new-char-name-wrapper {
	font-size: 1.3em;
}
.player-name-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#playerNameInput {
    width: 90%;
    margin-left: 5%;
}
.form-title {
    font-size: 1.4em;
}

.new-char-name-wrapper {
    font-size: 1.3em;
}

#new-char-name {
    border-radius: 100px;
    padding: 0 15px;
    margin-left: 10px;
    width: 40%;
    font-size: 1em;
}

.image-upload-wrapper {
    display: flex;
    justify-content: space-around;
}

#new-char-image {
    width: 0;
    height: 0;
    position: absolute;
}

#new-char-image-label {
    display: flex;
    padding: 5px 15px;
    background-color: #05c9fa;
    align-items: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    border-radius: 5px;
}

.upload-icon {
    width: 25px;
    height: 25px;
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    -webkit-clip-path: polygon(95% 5%, 95% 95%, 5% 95%, 5% 5%);
            clip-path: polygon(95% 5%, 95% 95%, 5% 95%, 5% 5%);
    margin-right: 10px;
}

#submit-form-button {
    background-color: #65d602;
    border: none;
    color: white;
    border-radius: 5px;
    height: 35px;
    padding: 7px 15px;
    cursor: pointer;
}
.score-board {
    display: flex;
    flex-direction: column;
}

.score-board-titles {
    display: flex;
    justify-content: space-between;
}

.team-title {
    text-align: left;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 5px 10px;
}

.blue-team-title {
    background-color: rgb(40, 96, 250);
    border-top-left-radius: 5px;
}

.red-team-title {
    background-color: rgb(251, 60, 60);
    border-top-right-radius: 5px;
}

.trophy-icon {
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    padding: 5px;
    background-position: center;
    background-color: rgb(70, 71, 69);
    width: 15%;
}

.player-list-container {
    display: flex;
    justify-content: space-evenly;
}

.player-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 5px 5px;
}

#blue-player-list {
    background-color: rgb(81, 127, 251);
    border-bottom-left-radius: 5px;
}

#red-player-list {
    background-color: rgb(255, 100, 100);
    text-align: right;
    border-bottom-right-radius: 5px;
}
.chat-container {
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    height: 100%;
    border-radius: 6px;
}

#chat-typer {
    background-color: rgb(20, 131, 48);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
}

#message-input {
    border-radius: 50px;
    font-size: 1em;
    border: none;
}

.send-icon {
    background-image: url(data:image/png;base64,);
    height: 30px;
    width: 30px;
    position: relative;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-clip-path: polygon(95% 5%, 95% 95%, 5% 95%, 5% 5%);
            clip-path: polygon(95% 5%, 95% 95%, 5% 95%, 5% 5%);
}

#send-message-icon {
    display: flex;
    background-color: #05c9fa;
    border-radius: 500px;
    width: 45px;
    align-items: center;
    justify-content: center;
}

#send-message-button {
    width: 0;
    height: 0;
    display: none;
}

.message-form {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

#chat-log {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    color: black;
    flex-grow: 1;
    padding: 5px 10px;
}

#chat-log * {
    word-break: break-word;
}

.blue-message {
    color: rgb(81, 127, 251);
}

.red-message {
    color: rgb(255, 100, 100);
}

.notification-message {
    color: rgb(121, 121, 121);
}

.chat-section {
    overflow: hidden;
    height: 100%;
}
